import React, {Component} from 'react';
import {Link} from 'react-router';
import moment from 'moment';

import {Loader} from '../../../widgets';
import GoogleMapDetails from '../../../widgets/googleMapDetails';
import{ YeahModal,SalesModal } from '../../../widgets/modals';

class Detail extends Component {
    constructor(props) {
        super(props);
        
        this.state = {
            details: props.data,
            showSalesModal: false
        }
    }
        
    openSalesModal(){
        this.setState({
            showSalesModal: true
        })
    }

    renderMap() {
        const {details} = this.state;

        if (details) {
            // console.log('detail.location', details.location);
            return <GoogleMapDetails geocoding={details.location.location}/>
        } else {
            return <Loader/>
        }
    }

    renderLead() {
        const {details} = this.state;
        if (details) {
            const {course, instructor} = details;
            return (
                <div className="lead">
                    <div className="program">Career Training Program</div>
                    <h1 className="title">{course.title}</h1>
                    <p>Learn from industry leaders.</p>
                    <div className="video-wrapper">
                        <span className="outer"></span>
                        <img src="https://s3-us-west-1.amazonaws.com/yeah-files/medias/photos/berkeley-yeah.jpg" />
                    </div>
                    <div className="introduction">
                        <div className="section" style={{'textAlign': 'left'}}>
                            <div className="subtitle">Enjoy a Fruitful Workshop</div>
                            <div>Did you know what college admissions are looking for in your transfer application?</div>
                            <br/>
                            <div>Did you know what big corporations value the most when it comes to recruiting process?</div>
                            <br/>
                            <div>In the past few years, we have researched and analyzed background of over 8000 students from community colleges and examined their college/job applications. Not surprisingly, internship, next to academic performance, by far, accounts for the most significant consideration. While a large number of students have abundant experiences in community service and club involvement, securing an internship as a community college student is by no means, an easy job. But, it is not un-doable.</div>
                            <br/>
                            <div>Today, at Yeah Career Workshop (Tech), four of our experienced guest speakers from Silicon Valley will discuss how CC students can get prepared for upcoming recruiting. Specifically, they will introduce what you need to learn outside of the class, what skills to develop and what interview questions to prepare before applying for internship positions. You will also get a chance to see how you can access our career training program and benefit from our valuable internship opportunities.</div> 
                        </div>
                        <div className="section instructor wider">
                            <div className="subtitle">
                                Amazing speakers
                            </div>
                            <div className="row">
                                {this.renderSpeackers()}
                            </div>
                        </div>
                        <div className="section wider">
                            <div className="container-fluid">
                               <div className="row">
                                <div className="col-sm-4 point">
                                    <img src="https://s3-us-west-1.amazonaws.com/yeah-files/icons/courses.svg"/>
                                    <div className="intro">
                                        <div className="intro-title">
                                            2&nbsp;hrs
                                        </div>
                                        <div className="description">
                                            Enjoy 2 hours brilliant workshop and valuable discussion session
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-4 point">
                                    <img src="https://s3-us-west-1.amazonaws.com/yeah-files/icons/projects.svg"/>
                                    <div className="intro">
                                        <div className="intro-title">
                                            Private Channel
                                        </div>
                                        <div className="description">
                                            Access to private channel for intership and career questions periodly
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-4 point">
                                    <img src="https://s3-us-west-1.amazonaws.com/yeah-files/icons/certificate.svg"/>
                                    <div className="intro">
                                        <div className="intro-title">
                                            Resume
                                        </div>
                                        <div className="description">
                                            Learn how take part in programs to power your resume and LinkedIn
                                        </div>
                                    </div>
                                </div>
                               </div> 
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        return (
            <div style={{padding: '100px'}}>
                <Loader/>
            </div>
        )
    }
    renderSpeackers(){
        if(this.state.details.speackers){
            return this.state.details.speackers.map((speaker, index) => {
                return (
                    <div className="profile" key={index}>
                        <div className="avatar">
                            <img src={speaker.imageURL}/>
                        </div>
                        <div className="name">
                            <a className="underline" href={speaker.linkedinURL} target="_blank">
                                {speaker.firstName} {speaker.lastName}
                            </a>
                        </div>
                        <div>{speaker.heighlightPosition.position}, {speaker.heighlightPosition.affiliation}</div>
                        <div><b>{speaker.industry} Expert</b></div>
                        <div className="bio">
                            {speaker.bio}
                        </div>
                    </div>
                )
            })
        }else{
            <div style={{padding: '100px'}}>
            </div>
        }
    }

    renderSyllabus() {
        const {details} = this.state;
        // console.log('details: ', details);
        if (details) {
            const {course, instructor} = details;
            return (
                <div className="syllabus">
                        {this.renderSyllabusCards(course)}
                </div>
            )
        }
    }
    renderSyllabusCards({heighlights, startingDate, hoursPerWeek}) {
        return heighlights.map((heighlight, index) => {
            const startingDateMoment = moment(startingDate);
            let date = startingDateMoment.add(index, 'weeks').format('dddd, MMMM Do, YYYY');
            // console.log(date)
            return (
                <div className="syllabus-card" key={heighlight.title}>
                    <div className="index-tag">
                        Class {index+1}
                    </div>
                    <div className="title">
                        {heighlight.title}
                    </div>
                    <div className="date">
                        <i className="fa fa-calendar" aria-hidden="true"></i>{date}
                    </div>
                    <hr/>
                    <div className="description">
                        
                    </div>
                    <br/>
                    <div className="info">
                        <i className="fa fa-newspaper-o" aria-hidden="true"></i>Material Preview: {hoursPerWeek/2} hours
                    </div>
                    <div className="info">
                        <i className="fa fa-university" aria-hidden="true"></i>Berkeley Campus Lecture: {hoursPerWeek} hours
                    </div>
                    <div className="info">
                        <i className="fa fa-cubes" aria-hidden="true"></i>Homework/Project: {hoursPerWeek/2} hours
                    </div>
                </div>
            )
        })
    }
    renderTakeaways(){
        const {details} = this.state;
        if (details) {
            const {course} = details;
            return (
                <div className="takeaways">
                <div className="subtitle">What you will get from this workshop</div>
                <ol className="check">
                   {this.renderTakeawayCards(course.takeaways)}
                </ol>
                </div>
            )
        }
    }
    renderTakeawayCards(takeaways) {
        return takeaways.map((takeaway, index) => {
            return (
                <li className="takeaway" key={index}>
                    {takeaway}
                </li>
            )
        })
    }
    renderEnrollNow(){
        const { details } = this.state;
        if (details) {
            const { course } = details;
            return (
                <div className="take_actions">
                <div className="card">
                    <div className="title">
                        {course.title}
                    </div>
                    <a href="https://www.eventbrite.com/e/32317127398" target="_blank">
                    <div className="action_button">
                        Sign Up Now
                    </div>
                    </a>
                    <div className="description">
                        2 hours workshop @<span>Haas, UC Berkeley</span>
                        <br/>
                        Learn how to get intership and succeed in the undustry
                    </div>
                </div>
                {this.state.showSalesModal && <YeahModal><SalesModal /></YeahModal>}
                </div>
            )
        }
    }
    render() {
        return (
            <div className="details-component">
                {this.renderLead()}
                {this.renderTakeaways()}
                {this.renderEnrollNow()}
            </div>
        )
    }
}

export default Detail;


